body {
  background-color: rgb(240, 240, 240);
}

.home_image {
  width: 100%;

  margin-bottom: -150px;
}
h3 {
  font-weight: 700;
}

.home_row {
  padding-top: 50px;
  margin-bottom: 60px;
  width: fit-content;
  display: flex;
  align-items: center;

  flex-wrap: wrap;

  flex-direction: column;
}

/**/

.header-banner {
  flex: 1;
  padding-top: 40vh;
  padding-right: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
  opacity: 0.95;
  text-align: center;
}

.header-banner h3 {
  font-weight: 200;
  font-size: 1rem;
}

.header-banner-title {
  font-size: 2.5rem;
  text-transform: capitalize;
  line-height: 1.3;
  letter-spacing: -1.5px;
  margin: 1.5rem 0;
}

.btn-trans {
  background: transparent;
  color: white;
  text-transform: capitalize;
  font-size: 1rem;
  font-weight: 200;
  padding: 0.8rem 2.2rem;
  border: 1px solid #fff;
  transition: var(--transition);
}

.btn-trans:hover {
  background: var(--emperor);
  border-color: var(--emperor);
}
